import { loadingMessagesConstants, modalTypeConstants } from '../../constants'

import { call, put, select, takeLatest } from 'redux-saga/effects'
import { callForHeader } from 'utils/HeadersCall'
import request from 'utils/request'
import { showLoadingScreen, hideLoadingScreen } from 'views/Modal/actions'
import { setAllValid } from './actions'
import { GENERATE_OPERAT, SAVE_INPUT_AREA } from './constants'

import { makeSelectId, makeSelectInput } from './selectors'
import { addNotification } from 'utils/addNotification'

export function * saveInputArea (action) {
  yield put(setAllValid())
  yield put(showLoadingScreen(
    modalTypeConstants.LOADING,
    { message: loadingMessagesConstants.SEND_OFFER }
  ))
  const data = yield select(makeSelectInput())
  const formData = new FormData()
  const blb = new Blob([JSON.stringify({ input: data })], { type: 'application/json' })
  formData.append('file', blb, 'params.json')
  const requestOptions = {
    method: 'POST',
    headers: yield call(callForHeader),
    body: formData
  }
  const requestUrl = `/OperatFiles/upload/${action.id}/convert`
  try {
    yield call(request, requestUrl, requestOptions)
    yield put(hideLoadingScreen())
  } catch (err) {
    console.log(err)
  }
}

export function * generateOperat () {
  const requestOptions = {
    method: 'POST',
    headers: yield call(callForHeader)
  }
  const operatId = yield select(makeSelectId())
  const requestUrl = `/Operat/id/${operatId}/generate`
  try {
    yield put(showLoadingScreen(
      modalTypeConstants.LOADING,
      { message: loadingMessagesConstants.GENERATE_OPERAT }
    ))
    yield call(request, requestUrl, requestOptions)
    yield put(hideLoadingScreen())
    yield call(addNotification, '', 'Operat zostal wygenerowany', 'success')
  } catch (err) {
    yield put(showLoadingScreen(modalTypeConstants.ERROR_MESSAGE, { message: 'Przy generowaniu operatu wystąpił błąd.' }))
  }
}

export default function * globalData () {
  yield takeLatest(SAVE_INPUT_AREA, saveInputArea)
  yield takeLatest(GENERATE_OPERAT, generateOperat)
}
