const createRow = (style) =>
  <Row>
    <Col sm='12' md={{ size: 3, offset: 4 }}>
      <Spinner
        color='primary'
        style={style}
        animation='border'
        variant='info'
      >
        {' '}
      </Spinner>
    </Col>
  </Row>

const createModalBody = () =>
  <ModalBody>
    {createRow({ width: '8rem', height: '8rem' })}
  </ModalBody>

const createModalBodyWithFooter = (dispatch) =>
  <ModalBody>
    {' '}
    {createRow({ width: '6rem', height: '6rem' })}
    {createModalFooter(
      dispatch)}
  </ModalBody>

const createModalFooter = (dispatch, cancel = false) =>
  <ModalFooter>
    <Button
      color='info'
      onClick={() => {
        dispatch(hideLoadingScreen())
      }}
    >
      {cancel ? 'anuluj' : 'ok'}
    </Button>
  </ModalFooter>

const createSignOperatModalFooter = (dispatch) =>
  <ModalFooter>
    <Button
      color='info'
      href='https://moj.gov.pl/nforms/signer/upload?xFormsAppName=SIGNER'
      target='blank'
    >
      Profil zaufany
    </Button>
    <Button
      color='info'
      onClick={() =>
        dispatch(showLoadingScreen(modalTypeConstants.OWN_CERTIFICATE))}
    >
      Własny certyfikat
    </Button>
    <Button
      color='danger'
      onClick={() => {
        dispatch(hideLoadingScreen())
      }}
    >
      Anuluj
    </Button>
  </ModalFooter>

