function SendOperat () {
  const dispatch = useDispatch()
  const [email, setEmail] = useState('')
  const [emails, setEmails] = useState([])
  const [sendToOwner, setSendToOwner] = useState(false)
  const data = useSelector(state => state.modal.data)
  console.log(data)
  return (
    <>
      <ModalHeader>Wyślij operat</ModalHeader>
      <ModalBody>
        <Form onSubmit={e => {
          e.preventDefault()
          if (emails.includes(email)) {
            return
          }
          setEmail('')
          setEmails(emails => [...emails].concat(email))
        }}
        >
          <FormGroup>
            <Label for='operat-email'>Podaj adres email</Label>
            <Input
              value={email}
              onChange={e => setEmail(e.target.value)}
              type='email'
              name='operat-email'
              required
            />
          </FormGroup>
          {
            data.operat.orderId !== globalConstants.EMPTY_GUID &&
              <FormGroup row style={{ marginLeft: '1em' }}>
                <Col>
                  <Input
                    id='checkbox2'
                    type='checkbox'
                    checked={sendToOwner}
                    onChange={() => setSendToOwner(sendToOwner => !sendToOwner)}
                  />
                  {' '}
                  <Label onClick={() => setSendToOwner(sendToOwner => !sendToOwner)}>
                    Wysłać do zleceniodawcy?
                  </Label>
                </Col>
              </FormGroup>
          }

          <Button type='submit'>Dodaj adres email</Button>
        </Form>
        <ul class='list-group'>
          {emails.map((e, idx) =>
            <li key={idx} class='list-group-item d-flex justify-content-between align-items-center'>
              {e}
              <span onClick={() => setEmails(emails => emails.filter(x => x !== e))}><i className='nc-icon nc-simple-remove' /></span>
            </li>
          )}
        </ul>
        <Button color='success' disabled={emails.length === 0 && sendToOwner === false} onClick={() => data.sendFunction(data.operat.id, emails, sendToOwner)}>Wyślij</Button>
      </ModalBody>
      {createModalFooter(dispatch, true)}
    </>
  )
}

function OwnCertificate () {
  const dispatch = useDispatch()
  const operatState = useSelector(state => state.operat)
  const [certificatePassword, setCertificatePassword] = useState('')
  return (
    <>
      <ModalHeader>Wybierz sposób w jaki chcesz podpisać operat</ModalHeader>
      <ModalBody>
        <Row>
          <Col>
            <FormGroup>
              <Input
                type='password'
                name='certificatePassword'
                id='certificatePassword'
                value={certificatePassword}
                placeholder='Hasło do certyfikatu'
                onChange={(e) => setCertificatePassword(e.target.value)}
              />
            </FormGroup>
          </Col>
          <Col>
            <Button
              onClick={() => {
                dispatch(showLoadingScreen(modalTypeConstants.LOADING, { message: loadingMessagesConstants.SING_OPERAT }))
                operatService.signPdf(operatState.id, { password: certificatePassword }).then(() => {
                  dispatch(hideLoadingScreen())
                }).catch(error => {
                  dispatch(showLoadingScreen(modalTypeConstants.ERROR_MESSAGE, { message: error }))
                })
              }}
              style={{ marginTop: '0px' }}
              color='success'
            >
              Podpisz operat
            </Button>
          </Col>
        </Row>
        <Row>
          <Col>Jeżeli nie posiadasz certyfikatu: <Button color='link' onClick={() => dispatch(showLoadingScreen(modalTypeConstants.NEW_CERTIFICATE, {}))}>Przejdź do generowania certyfikatu</Button></Col>
        </Row>
        <Row>
          <Col>
            <Button color='link' onClick={() => dispatch(showLoadingScreen(modalTypeConstants.SIGN_OPERAT, {}))}>Powrót</Button>
          </Col>
        </Row>
      </ModalBody>
    </>
  )
}

