import { createSelector } from 'reselect'
import { initialState } from './reducer'

const selectOperat = (state) => state.operat || initialState

const makeSelectInput = () =>
  createSelector(selectOperat, (state) => state.input)

const makeSelectId = () =>
  createSelector(selectOperat, state => state.id)
export {
  selectOperat,
  makeSelectInput,
  makeSelectId
}
